import React, { useState, useEffect, useRef, useCallback } from 'react';
import { Device } from '@twilio/voice-sdk';
import { useParams, Navigate } from 'react-router-dom';
import useBackgroundServiceManager from './BackgroundServiceManager';

const TwilioReceiver = () => {
  const { userId } = useParams();
  const [device, setDevice] = useState(null);
  // Use the background service manager
  const backgroundService = useBackgroundServiceManager();
  const [isConnected, setIsConnected] = useState(false);
  const [incomingConnection, setIncomingConnection] = useState(null);
  const [activeConnection, setActiveConnection] = useState(null);
  const [callStartTime, setCallStartTime] = useState(null);
  const [callDuration, setCallDuration] = useState('00:00');
  const [hasMicPermission, setHasMicPermission] = useState(false);
  const [connectionLogs, setConnectionLogs] = useState([]);
  const ringtoneRef = useRef(null);
  const durationTimer = useRef(null);
  const isConnecting = useRef(false);

  // Enhanced log entry function with reason
  const addConnectionLog = (status, reason = '') => {
    const timestamp = new Date().toLocaleTimeString();
    setConnectionLogs(prevLogs => {
      const newLog = { 
        status, 
        timestamp,
        reason: reason || (status === 'Disconnected' ? 'Unknown reason' : '')
      };
      // Keep only last 5 logs
      const updatedLogs = [newLog, ...prevLogs].slice(0, 5);
      return updatedLogs;
    });
  };

  // Modify the connection status updates to include reasons
  const updateConnectionStatus = (connected, reason = '') => {
    setIsConnected(connected);
    addConnectionLog(
      connected ? 'Connected' : 'Disconnected',
      reason
    );
  };

  const requestMicrophonePermission = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ 
        audio: true,
        video: false 
      });
      
      setTimeout(() => {
        stream.getTracks().forEach(track => track.stop());
      }, 1000);
      
      setHasMicPermission(true);
      return true;
    } catch (err) {
      setHasMicPermission(false);
      return false;
    }
  };

  const getAccessToken = async () => {
    if (!userId) {
      throw new Error('User ID is required');
    }
    try {
      const response = await fetch(`https://getcredentials-3757.twil.io/getCredentials?userId=${userId}`);
      const data = await response.json();
      if (!data.token) {
        throw new Error('No token received from server');
      }
      return data.token;
    } catch (error) {
      throw error;
    }
  };

  const connectToTwilio = useCallback(async () => {
    if (!userId || isConnecting.current || (device && isConnected)) return;

    try {
      isConnecting.current = true;
      const hasMicPermission = await requestMicrophonePermission();
      if (!hasMicPermission) {
        return;
      }
      
      // Ensure we have a wake lock to keep the device awake
      backgroundService.acquireWakeLock();

      const token = await getAccessToken();

      const twilioDevice = new Device(token, {
        enableRingingState: true,
        edge: ['ashburn', 'sydney', 'roaming', 'frankfurt', 'dublin'],
        region: 'gll',
        sounds: { incoming: null },
        allowIncomingWhileBusy: true,
        closeProtection: true,
        debug: true,
        maxReconnectAttempts: Infinity,
        reconnectTimeoutMs: 3000,
        // Add more aggressive connection settings
        keepaliveTimeoutMs: 30000,
        enableIceRestart: true,
        maxCallSignalingTimeoutMs: 30000,
        preflight: true
      });

      // Keep track of registration attempts
      let registrationAttempts = 0;
      const maxRegistrationAttempts = 5; // Increased from 3 to 5

      const attemptRegistration = async () => {
        try {
          await twilioDevice.register();
          registrationAttempts = 0; // Reset counter on success
          updateConnectionStatus(true);
          backgroundService.registerBackgroundSync();
          console.log('Device registered successfully');
        } catch (error) {
          console.error('Registration attempt failed:', error);
          registrationAttempts++;
          
          if (registrationAttempts < maxRegistrationAttempts) {
            console.log(`Retrying registration (attempt ${registrationAttempts + 1}/${maxRegistrationAttempts})`);
            // Exponential backoff for retries
            const backoffDelay = Math.min(1000 * Math.pow(2, registrationAttempts - 1), 10000);
            setTimeout(attemptRegistration, backoffDelay);
          } else {
            console.error('Max registration attempts reached');
            updateConnectionStatus(false);
            // Reset registration attempts after a longer delay and try again
            setTimeout(() => {
              registrationAttempts = 0;
              attemptRegistration();
            }, 30000);
          }
        }
      };

      twilioDevice.on('registered', () => {
        console.log('Device registered event');
        updateConnectionStatus(true);
        registrationAttempts = 0;
        backgroundService.registerBackgroundSync();
        // Start a periodic connection check
        const checkConnection = setInterval(() => {
          if (twilioDevice && twilioDevice.state !== 'registered') {
            attemptRegistration();
          }
        }, 15000);
        return () => clearInterval(checkConnection);
      });

      twilioDevice.on('unregistered', async () => {
        console.log('Device unregistered event');
        updateConnectionStatus(false, 'Device unregistered from Twilio');
        try {
          await attemptRegistration();
        } catch (error) {
          console.error('Failed to re-register after unregistered event:', error);
        }
      });

      twilioDevice.on('error', async (error) => {
        console.error('Device error event:', error);
        // Handle specific error types
        if (error.code === 31205) {
          updateConnectionStatus(false, 'Authentication token expired');
          // Get a new token and retry
          try {
            const newToken = await getAccessToken();
            await twilioDevice.updateToken(newToken);
            await attemptRegistration();
          } catch (tokenError) {
            console.error('Failed to refresh token:', tokenError);
            updateConnectionStatus(false, 'Failed to refresh authentication token');
          }
        } else {
          updateConnectionStatus(false, `Twilio error: ${error.message || 'Unknown error'}`);
          // For other errors, try to recover
          try {
            if (twilioDevice.state !== 'registered') {
              await attemptRegistration();
            }
          } catch (regError) {
            console.error('Failed to recover from device error:', regError);
          }
        }
      });

      twilioDevice.on('incoming', async (connection) => {
        // Ensure we're still registered when receiving calls
        if (twilioDevice.state !== 'registered') {
          await attemptRegistration();
        }

        const hasMicPermission = await requestMicrophonePermission();
        if (!hasMicPermission) {
          connection.reject();
          return;
        }

        if (ringtoneRef.current) {
          ringtoneRef.current.play().catch(() => {});
        }

        connection.on('cancel', () => {
          stopRingtone();
          setIncomingConnection(null);
        });

        connection.on('disconnect', () => {
          stopRingtone();
          setIncomingConnection(null);
          setActiveConnection(null);
          setCallDuration('00:00');
          clearInterval(durationTimer.current);
        });

        setIncomingConnection(connection);
      });

      // Initial registration
      await attemptRegistration();
      setDevice(twilioDevice);

    } catch (err) {
      console.error('Connection error:', err);
      // Implement exponential backoff for reconnection
      const retryConnection = (attempt = 1) => {
        const maxAttempts = 5;
        const delay = Math.min(1000 * Math.pow(2, attempt - 1), 30000);
        
        if (attempt <= maxAttempts && !isConnected) {
          setTimeout(() => {
            console.log(`Retry attempt ${attempt} of ${maxAttempts}`);
            connectToTwilio().catch(() => retryConnection(attempt + 1));
          }, delay);
        }
      };
      retryConnection();
    } finally {
      isConnecting.current = false;
    }
  }, [userId, device, isConnected, backgroundService]);

  const stopRingtone = () => {
    if (ringtoneRef.current) {
      ringtoneRef.current.pause();
      ringtoneRef.current.currentTime = 0;
    }
  };

  const handleAccept = () => {
    if (incomingConnection) {
      stopRingtone();
      incomingConnection.accept();

      incomingConnection.on('disconnect', () => {
        setActiveConnection(null);
        setCallDuration('00:00');
        clearInterval(durationTimer.current);
      });

      setActiveConnection(incomingConnection);
      setIncomingConnection(null);
      setCallStartTime(Date.now());
    }
  };

  const handleReject = () => {
    if (incomingConnection) {
      stopRingtone();
      incomingConnection.reject();
      setIncomingConnection(null);
    }
  };

  const handleHangUp = () => {
    if (activeConnection) {
      activeConnection.disconnect();
      setActiveConnection(null);
      setCallDuration('00:00');
      clearInterval(durationTimer.current);
    }
  };

  useEffect(() => {
    if (userId) {
      localStorage.setItem('twilioUserId', userId);
      
      // Initial connection
      connectToTwilio();
      
      // Send heartbeat
      backgroundService.sendHeartbeat();
      
      // More frequent connection checks
      const reconnectionInterval = setInterval(() => {
        if (!isConnected && device) {
          console.log('Connection check: Attempting to reconnect...');
          device.register().catch(() => {
            connectToTwilio();
          });
        } else if (!device) {
          console.log('Connection check: No device, creating new connection...');
          connectToTwilio();
        } else {
          console.log('Connection check: Connected');
        }
      }, 30000); // Check every 30 seconds
      
      // Handle visibility changes
      const handleVisibilityChange = () => {
        if (document.visibilityState === 'visible') {
          if (device && !isConnected) {
            device.register().catch(() => {
              connectToTwilio();
            });
          }
        }
      };
      
      document.addEventListener('visibilitychange', handleVisibilityChange);
      
      return () => {
        clearInterval(reconnectionInterval);
        document.removeEventListener('visibilitychange', handleVisibilityChange);
        stopRingtone();
        clearInterval(durationTimer.current);
      };
    }
    
    return () => {
      stopRingtone();
      clearInterval(durationTimer.current);
    };
  }, [userId, connectToTwilio, backgroundService, isConnected, device]);

  useEffect(() => {
    if (callStartTime) {
      durationTimer.current = setInterval(() => {
        const elapsed = Math.floor((Date.now() - callStartTime) / 1000);
        const minutes = String(Math.floor(elapsed / 60)).padStart(2, '0');
        const seconds = String(elapsed % 60).padStart(2, '0');
        setCallDuration(`${minutes}:${seconds}`);
      }, 1000);
    } else {
      clearInterval(durationTimer.current);
      setCallDuration('00:00');
    }
    return () => clearInterval(durationTimer.current);
  }, [callStartTime]);

  if (!userId) {
    return <Navigate to="/" replace />;
  }

  const containerStyle = {
    display: 'flex',
    justifyContent: 'flex-end',
    padding: '20px',
    gap: '10px'
  };

  const indicatorStyle = (color) => ({
    width: '20px',
    height: '20px',
    borderRadius: '50%',
    backgroundColor: color,
    transition: 'background-color 0.3s ease',
    boxShadow: '0 0 10px rgba(0,0,0,0.1)'
  });

  const indicatorContainerStyle = {
    position: 'relative',
    display: 'flex',
    alignItems: 'center'
  };

  const callOverlayStyle = {
    position: 'fixed',
    top: '20%',
    left: '50%',
    transform: 'translateX(-50%)',
    background: '#fff',
    border: '1px solid #ddd',
    borderRadius: '12px',
    padding: '30px',
    zIndex: 9999,
    boxShadow: '0 4px 20px rgba(0,0,0,0.2)',
    textAlign: 'center'
  };

  const buttonStyle = (bg) => ({
    margin: '10px',
    padding: '10px 20px',
    fontSize: '16px',
    borderRadius: '6px',
    border: 'none',
    cursor: 'pointer',
    backgroundColor: bg,
    color: '#fff'
  });

  return (
    <>
      <div style={containerStyle}>
        <div style={indicatorContainerStyle}>
          <div 
            style={indicatorStyle(
              hasMicPermission && isConnected ? '#4CAF50' : 
              !hasMicPermission ? '#ff4444' : 
              '#ffa500'
            )}
            title={
              hasMicPermission && isConnected ? 'Connected and ready' :
              !hasMicPermission ? 'Microphone permission required' :
              'Waiting for connection'
            }
          />
        </div>
        
        {/* Enhanced connection log display */}
        <div style={{
          position: 'fixed',
          bottom: '20px',  // Changed from top to bottom
          right: '20px',
          background: '#fff',
          padding: '15px',
          borderRadius: '12px',
          boxShadow: '0 4px 20px rgba(0,0,0,0.15)',
          maxWidth: '300px',
          width: '100%',
          zIndex: 1000
        }}>
          <div style={{
            fontSize: '14px',
            fontWeight: 'bold',
            marginBottom: '10px',
            color: '#333',
            borderBottom: '1px solid #eee',
            paddingBottom: '8px'
          }}>Connection Status Log</div>
          {connectionLogs.map((log, index) => (
            <div key={index} style={{
              fontSize: '13px',
              padding: '8px 0',
              borderBottom: index !== connectionLogs.length - 1 ? '1px solid #f5f5f5' : 'none'
            }}>
              <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                marginBottom: '2px'
              }}>
                <span style={{
                  color: log.status === 'Connected' ? '#4CAF50' : '#f44336',
                  fontWeight: '500'
                }}>{log.status}</span>
                <span style={{ color: '#666' }}>{log.timestamp}</span>
              </div>
              {log.reason && (
                <div style={{
                  fontSize: '12px',
                  color: '#666',
                  marginTop: '2px'
                }}>
                  {log.reason}
                </div>
              )}
            </div>
          ))}
        </div>
      </div>

      <audio ref={ringtoneRef} loop preload="auto">
        <source src="https://actions.google.com/sounds/v1/alarms/phone_alerts_and_rings.ogg" type="audio/ogg" />
        <source src="https://actions.google.com/sounds/v1/alarms/phone_alerts_and_rings.mp3" type="audio/mp3" />
        Your browser does not support the audio element.
      </audio>

      {incomingConnection && (
        <div style={callOverlayStyle}>
          <h2>Incoming Call</h2>
          <p>Do you want to accept it?</p>
          <button style={buttonStyle('#4CAF50')} onClick={handleAccept}>Accept</button>
          <button style={buttonStyle('#f44336')} onClick={handleReject}>Reject</button>
        </div>
      )}

      {activeConnection && (
        <div style={callOverlayStyle}>
          <h2>Call in Progress</h2>
          <p>Duration: {callDuration}</p>
          <button style={buttonStyle('#f44336')} onClick={handleHangUp}>Hang Up</button>
        </div>
      )}
    </>
  );
};

export default TwilioReceiver;
